				}else if(name == '油轮市场'){
					code = 'YLSC'
				}else if(name == '最新成交点数'){
					code = 'ZXCJDS'
				}
				var name = window.localStorage.setItem('channelName', name);
				var code = window.localStorage.setItem('channelCode', code);
				
				name = encodeURIComponent(name);
				code = encodeURIComponent(code);
			
				if(this.$route.query.name == 'SDZL'){
