				 			//跳登入页面
				 			return false;
				 		}
				 		
				 		
				 		//   判断走折线图   还是  走文章内容
				 		if(res.datas.echartList){
				 			this.onOff=true;
				 			
				 			this.tableList=res.datas.tableList;
