							}
							
							
							
							option.series[0].name=objArr[0].diliveryRegion;
							option.series[1].name=objArr[1].diliveryRegion;

							activeEach.setOption(option);

				 		}else{
				 			this.onOff=false;
				 			
				 		}
						 this.conten=res.datas;
						 //1111111111
						 if(this.conten.channelName == '油价与相关指数'){
							this.dialogVisible=true
						 }
